import React, { useState, useEffect } from "react";
import ArticlesList from "../Componest/articlesList";
import ArticlesContent from "./article-content";
import NotFoundPage from "./filenotfound";
import CommentsList from "../Componest/Commentslist";
import UpvotesSection from "../Componest/upvotesSection";
import AddCommentForm from "../Componest/AddCommentForm";
const ArticlePage = ({ match }) => {
  const name = match.params.name;
  const article = ArticlesContent.find((article) => article.name === name);

  const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });

  useEffect(() => {
    //starts fetch data
    const fetchData = async () => {
      const result = await fetch(`/api/articles/${name}`);
      const body = await result.json();
      console.log(body);
      setArticleInfo(body);
    };
    //fetchData nust be followed with a array block definition in order to stop a circuler
    fetchData();
  }, [name]);

  if (!article) return <NotFoundPage />;

  const otherArticles = ArticlesContent.filter(
    (article) => article.name !== name
  );

  return (
    <>
      <h1>{article.title} Page</h1>
      <UpvotesSection
        articleName={name}
        upvotes={articleInfo.upvotes}
        setArticleInfo={setArticleInfo}
      />
      {article.content.map((paragraph, key) => (
        <p key={key}>{paragraph}</p>
      ))}
      <CommentsList comments={articleInfo.comments} />
      <AddCommentForm articleName={name} setArticleInfo={setArticleInfo} />

      <h3>Other Articles</h3>
      <ArticlesList articles={otherArticles} />
    </>
  );
};
export default ArticlePage;
